"use client";

import { useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { Menu, X, ChevronDown } from "lucide-react";

export default function Navbar() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [langOpen, setLangOpen] = useState(false);
  const pathname = usePathname();

  // Available languages with corresponding root paths
  const languages = [
    { code: "EN", path: "/en" },
    { code: "KZ", path: "/kz" },
    { code: "RU", path: "/rus" },
  ];

  // Navigation links (same names for simplicity)
  const links = [
    { name: "Home", href: "/home" },
    { name: "News", href: "/news" },
    { name: "About", href: "/about" },
    { name: "Contact", href: "/contact" },
  ];

  // Determine current language based on path
  const currentLang =
    languages.find((lang) => pathname.startsWith(lang.path))?.code || "EN";

  return (
    <nav className="bg-white border-b border-gray-200 sticky top-0 z-50">
      <div className="max-w-6xl mx-auto px-6 py-4 flex items-center justify-between">
        {/* Logo */}
        <Link
          href={`/${currentLang.toLowerCase()}/home`}
          className="text-2xl font-bold text-blue-600"
        >
          MyNewsSite
        </Link>

        {/* Desktop nav */}
        <div className="hidden md:flex items-center gap-8">
          {links.map((link) => {
            // Full href includes language prefix
            const href = `/${currentLang.toLowerCase()}${link.href}`;
            const active = pathname === href;
            return (
              <Link
                key={href}
                href={href}
                className={`relative py-1 font-medium transition-colors ${
                  active ? "text-blue-600" : "text-gray-700 hover:text-blue-600"
                }`}
                aria-current={active ? "page" : undefined}
              >
                {link.name}
                <span
                  className={`absolute left-0 -bottom-1 h-[2px] rounded-full transition-all ${
                    active ? "w-full bg-blue-600" : "w-0 bg-blue-600"
                  }`}
                />
              </Link>
            );
          })}

          {/* Language selector */}
          <div className="relative">
            <button
              onClick={() => setLangOpen(!langOpen)}
              className="flex items-center gap-1 px-3 py-1 border border-gray-300 rounded hover:bg-gray-50 transition"
            >
              {currentLang}
              <ChevronDown size={16} />
            </button>

            {langOpen && (
              <div className="absolute right-0 mt-1 bg-white border border-gray-200 rounded shadow-md w-24 z-50">
                {languages.map((lang) => (
                  <Link
                    key={lang.code}
                    href={lang.path}
                    className={`w-full block px-3 py-2 hover:bg-gray-100 ${
                      lang.code === currentLang ? "font-semibold text-blue-600" : ""
                    }`}
                  >
                    {lang.code}
                  </Link>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Mobile menu button */}
        <button
          className="md:hidden p-2 rounded-md text-gray-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          onClick={() => setMenuOpen((s) => !s)}
          aria-expanded={menuOpen}
          aria-label="Toggle menu"
        >
          {menuOpen ? <X size={22} /> : <Menu size={22} />}
        </button>
      </div>

      {/* Mobile menu */}
      {menuOpen && (
        <div className="md:hidden bg-white border-t border-gray-200">
          {links.map((link) => {
            const href = `/${currentLang.toLowerCase()}${link.href}`;
            const active = pathname === href;
            return (
              <Link
                key={href}
                href={href}
                className={`block px-6 py-3 font-medium ${
                  active ? "text-blue-600 bg-gray-50" : "text-gray-700 hover:bg-gray-50"
                }`}
              >
                {link.name}
              </Link>
            );
          })}

          {/* Mobile language selector */}
          <div className="px-6 py-3 border-t border-gray-200">
            <p className="font-medium mb-2">Language</p>
            <div className="flex gap-2">
              {languages.map((lang) => (
                <Link
                  key={lang.code}
                  href={lang.path}
                  className={`px-3 py-1 rounded border ${
                    lang.code === currentLang
                      ? "bg-blue-600 text-white border-blue-600"
                      : "bg-white text-gray-700 border-gray-300 hover:bg-gray-50"
                  }`}
                >
                  {lang.code}
                </Link>
              ))}
            </div>
          </div>
        </div>
      )}
    </nav>
  );
}
